.page{
  width: 100vw;
  height: 100vh;
  overflow: hidden;

  &__content{
    position: relative;
    width: 100%;
  }

  &__wrapper{
    display: grid;
    grid-template-columns: 35vh 1fr;
  }
  
  @media (max-width: 1200px) {
    &__content{

    }

    &__wrapper{
      display: block;
    }
  }
}